.Main{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    color:white;
    background:url("https://images.wallpaperscraft.ru/image/samolet_fotoshop_zakat_prichal_120008_1920x1080.jpg");
    background-size: 100% 100%;
    align-content: center;
    text-align: center;
    position: relative;
}
.Cover {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,.5);
    z-index: 2;
}
.Container{
    max-width:1200px;
    margin: 0px auto;
}

.finish{
    color: white;
    text-align: center;
    font-weight: 700;
    font-size: 1.675rem;
    line-height: 1.37;
}
.fr{
    margin-top: 45vh;
}
@media (max-width: 1200px){
    .Container{
        max-width: 970px;
    }
}
@media (max-width: 992px){
    .Container{
        max-width: 750px
    }
}
@media (max-width: 760px) {
    .Main {
        background:url("https://i.pinimg.com/564x/a4/5e/08/a45e0834614f9a857abfec647e1efa61.jpg");
    }

}